//import Vue from 'vue'

const capitalPolishWord = /^[A-Z][a-z,]+$/;
const capitalPolishLastname = /^[A-Z][a-z,-]+$/;

export default {
  notEmptyRules: [
    v => !!v || 'To pole jest wymagane'
  ],
  pickedUserObjRule: [
    v => !!v && v !== null || 'To pole jest wymagane'
  ],
  classnameRules:[
    v => !!v || 'To pole jest wymagane',
    v => v && v.length >= 3 || "Za krtka nazwa klasy",
    v => v && /\d[A-Z]+\d$/.test(v) || "Bdna nazwa klasy"
  ],
  targetRules:[
    v => v && v.length >= 5 || "Za krtka nazwa celu zbirki"
  ],
  targetDescRules:[
    v => v && v.length >= 15 || "Za krtki opis zbirki"
  ],
  amountRules:[
    v => !!v || 'To pole jest wymagane',
    v => /^(\d*[.]\d{2}|\d*)$/.test(v) || 'Bdna forma kwoty'
  ],
  datesRules:[
    v => !!v || 'To pole jest wymagane',
    v => v.length == 2 || 'Wyznacz dat pocztku i koca zbirki'
  ],
  nameRules: [
    v => !!v || 'Imi i nazwisko jest wymagane',
    v => (v && v.length >= 5) || 'Imi i nazwisko musi mie conajmniej 5 znakw',
    v => {
      if(v !== null){
        const parts = v.split(' ');
        if(parts.length !== 2){
          return 'Bdna forma';
        }

        if(!capitalPolishWord.test(parts[0]) || !capitalPolishLastname.test(parts[1])){
          return 'Bdna forma';
        }

        return true;
      } else {
        return 'Imi i nazwisko jest wymagane';
      }
    }
  ],
  loginRules: [
    v => !!v || 'Login jest wymagany',
    v => (v && v.length >= 5) || 'Login musi mie conajmniej 5 znakw'
  ],
  passwordRules: [
    v => !!v || 'Haso jest wymagane',
    v => (v && v.length >= 8) || 'Haso musi mie conajmniej 8 znakw',
    v => /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#$%^&*])(?=.{8,})/.test(v) || 'Haso musi by silniejsze!'
  ],
  emailRules: [
    v => !!v || 'E-mail jest wymagany',
    v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail musi by poprawny'
  ]
};